@charset "UTF-8";
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

/* remember to define focus styles! */
:focus {
  outline: 0; }

/* remember to highlight inserts somehow! */
ins {
  text-decoration: none; }

del {
  text-decoration: line-through; }

/* tables still need 'cellspacing="0"' in the markup */
table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  box-sizing: border-box;
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Lato", Arial, Helvetica, Tahoma, sans-serif;
  font-size: 14px;
  color: #000;
  background: #fff; }

a {
  color: #000;
  text-decoration: none; }
  a:hover {
    color: #1a1a1a;
    text-decoration: underline; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

p {
  line-height: 1; }

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Playfair Display", sans-serif;
  font-weight: 700;
  line-height: 1;
  color: #000; }

h1, .h1 {
  font-size: 30px;
  margin: 15px 0; }

h2, .h2 {
  font-size: 28px;
  margin: 15px 0; }

h3, .h3 {
  font-size: 23px;
  margin: 15px 0; }

h4, .h4 {
  font-size: 14px;
  margin: 10px 0; }

h5, .h5 {
  font-size: 14px;
  margin: 5px 0; }

h6, .h6 {
  font-size: 13px;
  margin: 2px 0; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-center {
  text-align: center; }

.text-justify {
  text-align: justify; }

.text-nowrap {
  white-space: nowrap; }

.text-lowercase {
  text-transform: lowercase; }

.text-uppercase {
  text-transform: uppercase; }

.text-capitalize {
  text-transform: capitalize; }

.text-brand {
  color: #f09d66; }

.text-inverse {
  color: #fff; }

.flex {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex; }

.flex-row {
  flex-direction: row; }

.justify-content-center {
  justify-content: center; }

.clearfix:before, .clearfix:after {
  content: " ";
  display: table; }

.clearfix:after {
  clear: both; }

.center-block {
  display: block;
  margin-left: auto;
  margin-right: auto; }

.pull-right {
  float: right !important; }

.pull-left {
  float: left !important; }

.hidden {
  opacity: 0; }

.visible {
  opacity: 1; }

.img-responsive {
  display: block;
  width: 100%;
  height: auto; }

